@font-face {
  font-family: Novelist;
  src: url("Novelist.c5bbe7ca.otf");
  font-weight: normal;
}

:root {
  --color-dark: #212122;
}

* {
  box-sizing: border-box;
}

body {
  color: #333;
  margin: 0;
  font: 14px / 1.2 Arial, sans-serif;
  position: relative;
}

h1, h2, h3, h4 {
  font-family: Roboto Condensed, Arial, sans-serif;
}

h2 {
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.2;
}

h4 {
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 19px;
}

.container {
  max-width: 930px;
  margin: 0 auto;
}

@media screen and (width >= 930px) {
  .container {
    box-sizing: content-box;
  }
}

.content {
  padding: 0 16px;
}

.header {
  background: #212122;
  width: 100%;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 16px;
  display: flex;
}

@media screen and (width >= 768px) {
  .header__buttons {
    display: none;
  }
}

.header__button {
  background: none;
  border: 0;
  width: 30px;
  height: 30px;
  margin-right: 20px;
}

.header__button:last-of-type {
  margin-right: 0;
}

.header__button_search {
  background-image: url("search.90aecc8a.svg");
}

.header__button_profile {
  background-image: url("profile.94fc89cf.svg");
}

.header__button_menu {
  background-image: url("menu.a11fae8a.svg");
}

.header__nav {
  display: none;
}

@media screen and (width >= 768px) {
  .header__nav {
    align-items: center;
    gap: 20px;
    display: flex;
  }
}

.header__sign-in {
  color: #fff;
  background: none;
  border: 1px solid #ccc;
  border-radius: 61px;
  height: 38px;
  padding: 0 16px;
}

.header__right {
  display: none;
}

@media screen and (width >= 768px) {
  .header__right {
    align-items: center;
    display: flex;
  }
}

.header__nav-link {
  color: #fff;
  text-decoration: none;
}

.header__left {
  align-items: center;
  gap: 20px;
  display: flex;
}

.cards {
  grid-template-columns: 1fr 1fr;
  gap: 20px 10px;
  width: 100%;
  display: grid;
}

@media screen and (width >= 768px) {
  .cards {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.card {
  text-align: center;
  position: relative;
}

.card__img {
  width: 100%;
}

.card__price_old {
  color: #aeb5b9;
  position: relative;
}

.card__price_old:after {
  content: "";
  background: #aeb5b9;
  width: 100%;
  height: 1px;
  position: absolute;
  top: 9px;
  left: 0;
}

.card__sale {
  background-image: url("sale.66299b30.png");
  background-size: cover;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 10px;
  left: 10px;
}

.order-background {
  background: #00000080;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.order {
  background: #fff;
  width: 600px;
  height: 600px;
  padding: 32px 16px;
}

.order__input {
  border: 1px solid #999;
  width: 100%;
  height: 38px;
  margin-bottom: 20px;
  padding: 0 10px;
}

.order__input:focus {
  border-color: #0f8fee;
  outline: none;
}

.order__button {
  color: #fff;
  background: #0f8fee;
  border: none;
  width: 100%;
  height: 38px;
}

.order__button:active {
  background: #3781d0;
  transform: translate(1px, 1px);
}

.select {
  appearance: none;
  background-image: url("arrow.6f6cc518.svg"), linear-gradient(to right, #999 1px, #0000 1px);
  background-position: 100%;
  background-repeat: no-repeat;
  background-size: 28px;
  border: 1px solid #999;
  border-radius: 0;
  width: 100%;
  height: 38px;
  margin-bottom: 20px;
  padding: 0 10px;
}

.order__pickup {
  border: 1px solid #999;
  margin-bottom: 20px;
  display: flex;
}

.order__pickup label {
  text-align: center;
  color: #666;
  width: 50%;
  height: 38px;
  line-height: 38px;
  display: inline;
}

.order__pickup label:first-of-type {
  border-right: 1px solid #999;
}

.order__pickup input[type="radio"] {
  display: none;
}

.order__radio:checked + label {
  color: #333;
  background: #eee;
}

.footer {
  background-color: var(--color-dark);
  flex-direction: column;
  padding: 30px 15px;
  display: flex;
}

.footer__social {
  flex-direction: row;
  gap: 15px;
  margin-bottom: 30px;
  display: flex;
}

.footer__social img {
  cursor: pointer;
  width: 36px;
  height: 36px;
}

.footer__download {
  flex-direction: row;
  gap: 8px;
  margin-bottom: 30px;
  display: flex;
}

.footer__download img {
  cursor: pointer;
  width: 140px;
  height: 40px;
}

.footer__logo {
  width: 46px;
  height: 46px;
  display: none;
}

.footer hr {
  border-color: #666;
  width: 100%;
  height: 1px;
  margin-bottom: 15px;
}

.footer p {
  color: #999;
}

@media (width >= 768px) {
  .footer {
    padding: 30px 35px;
  }

  .footer__top {
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    display: flex;
  }
}

@media (width >= 1024px) {
  .footer {
    padding: 40px 175px;
  }

  .footer__social, .footer__download {
    align-items: center;
  }

  .footer__logo {
    display: block;
  }
}

.info {
  background-color: var(--color-gray);
  flex-direction: column;
  gap: 30px;
  padding: 30px 15px;
  display: flex;
}

.info__block {
  flex-direction: row;
  align-items: center;
  gap: 20px;
  display: flex;
}

.info__block p {
  color: #333;
}

.info__block img {
  width: 40px;
  height: 40px;
}

@media (width >= 768px) {
  .info {
    flex-direction: row;
    justify-content: space-between;
    padding: 30px 35px;
  }
}

@media (width >= 1200px) {
  .info {
    padding: 40px 175px;
  }
}

